.containerLine{
    width: 80%;
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid;
}

.containerColumn{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 30px;
    width: 80%;
    height: 100%;
    margin: 0 auto;
    border: 1px solid;
}